.cardsContainer {
	display: grid;
	grid-template-rows: repeat(4, 14rem);
	grid-template-columns: repeat(5, 14rem);
	grid-gap: 1.5rem;
}
.container {
	display: flex;
	flex-direction: column;

	position: relative;
	transition: all .2s ease-in-out;

	/* height: 100%; */
	min-height: 100vh;
}
.content {
	display: flex;
	flex-grow: 1;
	justify-content: center;
	align-items: center;
	align-content: center;
	padding-top: 4.5rem;
}
.load {
	position: absolute;
	width: 100vw;
	height: 100vh;
}

.hide,
.hideWar,
.showWar {
	display: none;
}

.woof {
	color: #4d9de0;
}
.exc {
	color: #eef4d4;
}
.showConn {
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	touch-action: none;
	overflow: hidden;
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 100;
	background-color: #fa7268;
	font-family: 'Inconsolata', monospace;
	font-size: 3.5rem;
	padding: 0 4rem;
}

@media (max-width: 75em) {
	.cardsContainer {
		grid-template-rows: repeat(4, 13rem);
		grid-template-columns: repeat(5, 13rem);
		grid-gap: 2.8rem;
	}
	.container {
		height: 100%;
	}
	.content {
		padding-bottom: 3.5rem;
	}
}

@media (max-width: 56.25em) {
	.cardsContainer {
		grid-template-rows: repeat(4, 12rem);
		grid-template-columns: repeat(5, 12rem);
		grid-gap: 3rem;
	}
	.container {
		/* height: 100%; */
	}
	.content {
		padding-bottom: 5rem;
	}
}

@media (max-width: 50em) {
	/* 820px:16px=50em  1em=16px */
	.cardsContainer {
		grid-template-rows: repeat(5, 12rem);
		grid-template-columns: repeat(4, 12rem);
		grid-gap: 2.5rem;
	}
	.content {
		padding-top: 2.5rem;
		padding-bottom: 5rem;
	}
	.container {
		/* height: 100%; */
	}
}

@media (max-width: 37.5em) {
	.cardsContainer {
		grid-template-rows: repeat(5, 10.5rem);
		grid-template-columns: repeat(4, 10.5rem);
		grid-gap: 1.3rem;
	}
	.container {
		/* height: 100%; */
	}
	.content {
		padding-bottom: 3.5rem;
	}
}

@media (max-width: 32.5em) {
	.cardsContainer {
		/* grid-template-rows: repeat(5, 10.5rem); */
		grid-template-columns: repeat(3, 10.5rem);
		grid-gap: 1.3rem;
	}
	.container {
		height: 100%;
	}
}

@media (orientation: landscape) {
	.showWar {
		position: fixed;
		top: 0;
		left: 0;
		width: 100vw;
		height: 100vh;
		touch-action: none;
		overflow: hidden;
		display: flex;
		justify-content: center;
		align-items: center;
		z-index: 100;
		background-color: #fa7268;
		font-family: 'Inconsolata', monospace;
		font-size: 3.5rem;
	}
}

